/* Variables */
/* Google font */
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700;800;900&family=Pacifico&display=swap");
/* Reset */
*,
::after,
::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Global styling */
html {
  scroll-behavior: smooth;
}

body {
  font-family: "Montserrat", sans-serif;
  background: #fbfcfe;
  color: #0a1219;
  line-height: 1.5;
  font-size: 1rem;
  overflow-x: hidden;
}
@media (max-width: 63.9375em) {
  body.not-scroll {
    overflow: hidden;
  }
}

h1, h2, h3 {
  font-weight: 500;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.8rem;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
  cursor: pointer;
}

img {
  max-width: 100%;
  height: 100%;
  margin: 0 auto;
  display: block;
}

.container {
  max-width: 75rem;
  margin: 0 auto;
}

.title-section {
  margin-bottom: 3rem;
}

.subtitle-section {
  color: #4682b4;
  font-size: 1.5rem;
  margin: 1rem 0 0.3rem 0;
}
@media (min-width: 40em) {
  .subtitle-section {
    margin: 0;
  }
}

.title-section::after {
  content: "";
  position: absolute;
  width: 10rem;
  height: 4px;
  border-bottom: 2px solid #4682b4;
}

.section-center {
  padding: 4rem 1rem;
  max-width: 75rem;
  margin: 0 auto;
}

.primary-btn {
  display: inline-block;
  background-color: #4682b4;
  color: #fbfcfe;
  padding: 0.6rem 1rem;
  font-size: 1rem;
  font-weight: 500;
  text-transform: uppercase;
  cursor: pointer;
  transition: all 0.4s linear;
  border: 1px solid #4682b4;
}
.primary-btn:hover {
  background: transparent;
}

.lead {
  font-size: 1rem;
  color: #0a1219;
  line-height: 1.6;
  margin: 0.5rem 0;
}

.social-icons {
  font-size: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 4rem;
}
.social-icons a {
  padding: 0.5rem;
  color: #fbfcfe;
  transition: all 0.4s linear;
}
.social-icons a:hover {
  color: #4682b4;
}

@keyframes hero-load {
  0% {
    transform: translateY(-20px);
    opacity: 0;
  }
  100% {
    transform: translateY(0px);
    opacity: 1;
  }
}
@keyframes bounce {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}
