.header .navbar {
  background-color: transparent;
  position: relative;
  overflow: hidden;
}
@media (min-width: 40em) {
  .header .navbar .container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.5rem 1rem;
  }
}
.header .navbar.fixed-navbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: #0a1219;
  color: #0a1219;
  z-index: 10;
  overflow: hidden;
}
.header .navbar.fixed-navbar .navbar__header .navbar__logo {
  color: #fbfcfe;
}
.header .navbar.fixed-navbar .navbar__header .navbar__burger span {
  background-color: #fbfcfe;
}
.header .navbar.fixed-navbar .navbar__menu {
  transition: all 0.4s linear;
}
.header .navbar.fixed-navbar .navbar__menu li a {
  color: #fbfcfe;
  transition: all 0.4s linear;
}
.header .navbar.fixed-navbar .navbar__menu li a:hover {
  color: #4682b4;
}
.header .navbar__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem 1rem;
}
.header .navbar__logo {
  font-size: 2rem;
  font-family: "Pacifico", cursive;
  color: #fbfcfe;
}
@media (min-width: 40em) {
  .header .navbar__burger {
    display: none;
  }
}
.header .navbar__burger > span {
  display: block;
  width: 26px;
  height: 2px;
  background-color: #fbfcfe;
  transition: all 0.4s linear;
  transform-origin: 3px 1px;
}
.header .navbar__burger > span:not(:last-child) {
  margin-bottom: 5px;
}
.header .navbar.open .navbar__burger > span:first-child {
  transform: rotate(45deg);
}
.header .navbar.open .navbar__burger > span:nth-child(2) {
  opacity: 0;
}
.header .navbar.open .navbar__burger > span:last-child {
  transform: rotate(-45deg);
}
.header .navbar__menu {
  height: 0;
  overflow: hidden;
  transition: all 0.4s linear;
}
@media (min-width: 40em) {
  .header .navbar__menu {
    height: auto;
    display: flex;
  }
}
.header .navbar__menu.open {
  height: auto;
}
.header .navbar__menu li a {
  color: #fbfcfe;
  font-size: 1rem;
  font-weight: 500;
  text-transform: capitalize;
  letter-spacing: 2px;
  display: block;
  padding: 0.5rem 1rem;
  transition: all 0.4s linear;
}
@media (min-width: 40em) {
  .header .navbar__menu li a {
    padding: 0;
    margin: 0 0.5rem;
  }
}
.header .navbar__menu li a:hover {
  color: #fbfcfe;
  padding-left: 1.5rem;
}
@media (min-width: 40em) {
  .header .navbar__menu li a:hover {
    background-color: transparent;
    color: #0a1219;
    padding: 0;
  }
  .header .navbar__menu li a:hover:hover {
    color: #4682b4;
  }
}
.header .btn-down {
  font-size: 1.5rem;
  position: absolute;
  bottom: 2rem;
  left: 1rem;
  background: #4682b4;
  width: 2.5rem;
  height: 2.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 4px;
  color: #fbfcfe;
  animation: bounce 2s ease-in-out infinite;
}
@media (min-width: 40em) {
  .header .btn-down {
    bottom: 4.5rem;
    left: 4rem;
  }
}

.hero {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 90vh;
  animation: hero-load 300ms ease-in forwards 0.5s;
  transform: translateY(-20px);
  opacity: 0;
}
.hero__intro {
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
  color: #fbfcfe;
  padding: 1rem;
}
.hero__intro h3 {
  font-weight: 300;
}
.hero__intro h1 {
  margin: 0;
  padding: 0;
}
.hero__intro h2 {
  font-size: 1.8rem;
  margin-bottom: 2rem;
}
@media (min-width: 40em) {
  .hero__intro h2 {
    font-size: 2rem;
  }
}

.about__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
@media (min-width: 40em) {
  .about__content {
    flex-direction: row;
    align-items: flex-start;
  }
}
.about__img {
  max-width: 100%;
  height: auto;
  padding: 0.5rem;
}
.about__img img {
  min-width: 300px;
  height: 350px;
}
.about__info {
  margin: 2rem 0;
}
@media (min-width: 40em) {
  .about__info {
    margin: 0 0 0 2rem;
  }
}
.about__info a {
  color: #fbfcfe;
}
.about__info a:hover {
  color: #0a1219;
}
@media (min-width: 40em) {
  .about__skills {
    margin-top: 2rem;
  }
}
.about__certifications .certifications {
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 3rem 2rem;
  margin-top: 1rem;
}
@media (min-width: 40em) {
  .about__certifications .certifications {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 64em) {
  .about__certifications .certifications {
    grid-template-columns: repeat(3, 1fr);
  }
}
.about__certifications .certifications a img:hover {
  transform: scale(1.05);
  transition: all 0.4s linear;
}
.about__certifications img {
  width: 450px;
}

.skills__content {
  display: flex;
  flex-direction: column;
  margin: 1rem;
  font-size: 1.1rem;
}
.skills__content h5 {
  font-size: 1.2rem;
  font-weight: 500;
}
.skills__content .skill-item {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(140px, 1fr));
  gap: 1rem;
}
.skills__content .skill-item .skill-icon {
  display: flex;
  align-items: center;
  padding: 1rem;
}
.skills__content .skill-item .skill-icon span {
  padding: 0.5rem;
}

.projects__menu {
  display: flex;
  justify-content: left;
  align-items: center;
  flex-wrap: wrap;
  margin-bottom: 2rem;
}
.projects__menu li {
  font-size: 1rem;
  text-transform: capitalize;
  padding: 1rem;
  background: transparent;
  border: 2px solid #4682b4;
  cursor: pointer;
  margin: 0.5rem;
  transition: all 0.4s linear;
}
.projects__menu li:hover {
  background-color: #4682b4;
  color: #fbfcfe;
}
.projects__menu li.active, .projects__menu li.current {
  background-color: #4682b4;
  color: #fbfcfe;
}
.projects__content {
  display: grid;
  grid-template-columns: 1fr;
  justify-items: center;
  gap: 1rem;
}
@media (min-width: 40em) {
  .projects__content {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 64em) {
  .projects__content {
    grid-template-columns: repeat(3, 1fr);
  }
}
.projects__content .project.delete {
  display: none;
}
.projects__content .project {
  position: relative;
}
.projects__content .project__img {
  max-width: 100%;
  height: auto;
}
.projects__content .project__img img {
  width: 400px;
  height: 300px;
}
.projects__content .project__info {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  color: #fbfcfe;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  transition: all 0.4s linear;
  opacity: 0;
}
.projects__content .project__info:hover {
  opacity: 1;
}
.projects__content .project__info--blur {
  backdrop-filter: blur(5px);
}
.projects__content .project__info h4 {
  font-size: 1.8rem;
  text-align: center;
}
.projects__content .project__info .project-link {
  margin: 2rem 0;
}
.projects__content .project__info .project-link a {
  font-size: 2.4rem;
  color: #fbfcfe;
  margin: 1rem;
  text-transform: uppercase;
  transition: all 0.4s linear;
}
.projects__content .project__info .project-link a:hover {
  color: #4682b4;
}

