.contact h3 {
  margin-top: -1rem;
  margin-bottom: 4rem;
  color: #0a1219;
}
.contact__content {
  display: grid;
  grid-template-columns: 1fr;
  gap: 2rem;
}
@media (min-width: 40em) {
  .contact__content {
    grid-template-columns: repeat(3, 1fr);
  }
}
.contact__info {
  font-size: 1.5rem;
  margin-bottom: 1rem;
  color: #0a1219;
  border: 1px solid #4682b4;
  padding: 1rem;
}
.contact__info h4 {
  font-size: 1.5rem;
  font-weight: 500;
  color: #0a1219;
  text-transform: capitalize;
}
@media (min-width: 40em) {
  .contact__info h4 {
    font-size: 1.2rem;
  }
}
.contact__info a {
  color: #0a1219;
  font-size: 1.2rem;
  cursor: pointer;
}
@media (min-width: 40em) {
  .contact__info a {
    font-size: 1rem;
  }
}
.contact__info a:hover {
  color: var(--secondary-color);
  font-weight: 500;
}

.footer {
  font-size: 1rem;
  display: flex;
  justify-content: center;
  text-align: center;
  align-items: center;
  flex-direction: column;
  background: #0a1219;
  color: #fbfcfe;
  width: 100%;
  padding: 1rem;
}
.footer .social-icons {
  margin: 0;
}

/* ******************************** */
/* Top link */
.up-link {
  display: none;
}
@media (min-width: 40em) {
  .up-link {
    font-size: 1.5rem;
    position: fixed;
    bottom: 3rem;
    right: 3rem;
    background-color: #4682b4;
    width: 2.5rem;
    height: 2.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 4px;
    color: #fbfcfe;
    visibility: hidden;
    z-index: -100;
    animation: bounce 2s ease-in-out infinite;
    overflow: hidden;
    box-shadow: 0 0 10px var(#0a1219);
  }
}

.up-link.show {
  visibility: visible;
  z-index: 100;
}